import { createBrowserRouter,createHashRouter, RouterProvider, Outlet, useLocation } from "react-router-dom";
import Navbar from "./components/navbar/Navbar";
import Home from "./pages/home/Home";
import "./app.scss";
import Single from "./pages/singleNews/Single";
import Footer from "./components/footer/Footer";
import Sidebar from "./components/sidebar/Sidebar";
import OtherPages from "./pages/otherPages/OtherPages";
import Sections from "./pages/sections/Sections";
import News from "./components/newsTicker/News";
import useFetch from "./hooks/UseFetch";
import SingleArticle from "./pages/singleArticle/SingleArticles";
import Articles from "./pages/articles/Articles";
import { useEffect, useState } from "react";

function App() {
  const { data } = useFetch(`news/newsticker`);
  const words = data.length > 0 && data.map((el) => {
    return el.title
  })
  // const location = useLocation();
  // const [p, setP] = useState(window.location.href.split("/")[4])
  // const [style, setStyle] = useState({display: "block"})
  // const path = window.location.href.split("/")[5]
  // console.log(p);
  // useEffect(() => {
  //   setP(window.location.href.split("/")[4])
  //   if(p) {
  //     setStyle({display: "flex"})
  //   } else {
  //     setStyle({display: "block"})

  //   }
  // },[p])
  const Layout = () => {
    return (
      <div className="mainContent">
        <Navbar />
        <div className="middleContent">
          <Outlet />
          {/* {p && <Sidebar />} */}
        </div>
        <Footer />
        {/* {data.length > 0 && <News words={words}/>} */}

      </div>
    );
  };

  const router = createHashRouter([
    {
      path: "/",
      element: <Layout />,
      children: [
        {
          index: true,
          element: <Home />,
        },
        {
          path: "/news/:id",
          element: <Single />,
        },
        {
          path: "/arts/:id",
          element: <SingleArticle />,
        },
        {
          path: "/page/:id",
          element: <OtherPages />,
        },
        {
          path: "/section/:id",
          element: <Sections />,
        },
        {
          path: "/articles",
          element: <Articles />,
        },
      ],
    },
  ]);
  return (
    <div className="app">
      <RouterProvider router={router} />
    </div>
  );
}

export default App;
